<template>
  <div
    class="custom-select"
    :tabindex="tabindex"
    @blur="open = false"
  >
    <div class="selected" :class="{ open: open }" @click="toggleOpen">
      {{ modelValue }}
      <img
        :src="require('@/assets/img/arrow-down.svg')"
        class="arrow"
        :class="{ open: open }"
        alt=""
      />
    </div>
    <div class="items" :class="{ selectHide: !open }">
      <div
        v-for="(option, index) in options"
        :key="index"
        @click="selectOption(option)"
        :class="{ 'selected-option': option === modelValue }"
      >
        {{ option }}
      </div>
    </div>
  </div>
</template>

<script>
export default {
  props: {
    options: {
      type: Array,
      required: true,
    },
    modelValue: {
      type: String,
      required: true, // Убедитесь, что модель данных (значение) передается
    },
    tabindex: {
      type: Number,
      required: false,
      default: 0,
    },
  },
  data() {
    return {
      open: false,
    };
  },
  methods: {
    toggleOpen() {
      this.open = !this.open;
    },
    selectOption(option) {
      this.open = false;
      this.$emit("update:modelValue", option); // Используем событие для обновления modelValue
    },
  },
};
</script>

