  background-color: #fff;
  height: 100%;
  padding-left: 5px;
  cursor: pointer;
  user-select: none;
  font-family: var(--font-family);
  overflow: hidden;
}

.custom-select .items {
  border-radius: 10px;
  overflow: hidden;
  overflow-y: auto;
  border: 1.5px solid var(--main);
  position: absolute;
  background-color: #fff;
  margin-top: 10px;
  left: 0;
  right: 0;
  z-index: 1;
  max-height: 200px;
}

.custom-select .items div {
  padding-left: 1em;
  padding: 5px;
  cursor: pointer;
  user-select: none;
  font-family: var(--font-family);
}

.custom-select .items div:hover {
  background-color: #e1ebff;
}

.selectHide {
  display: none;
}
.custom-select .items div.selected-option {
  color: var(--main-color);
}
.custom-select .arrow {
  transition: transform 0.3s ease;
  width: 12px;
