<!-- PlotlyCsv3d.vue -->
<template>
  <div>
    <div :id="chartId" class="plotly-chart"></div>
  </div>
</template>

<script>
import Plotly from "plotly.js-dist-min";

export default {
  props: {
    csvData: {
      type: Array,
      required: true,
      default: () => [],
    },
    view: {
      type: String,
      default: "frontView",
    },
    layout: {
      type: Object,
      required: false,
      default: () => ({
        xaxis: { title: "X (км)" },
        yaxis: { title: "Y (км)" },
        zaxis: { title: "Z (км)" },
        margin: { l: 1, r: 1, b: 1, t: 1 },
        scene: {
          camera: {
            eye: { x: 1.25, y: 1.25, z: 1.25 },
          },
        },
      }),
    },
  },
  data() {
    return {
      chartId: `plotly-chart-${Math.random()
        .toString(36)
        .substr(2, 9)}`,
      layouts: {
        frontView: {
          scene: {
            camera: { eye: { x: 2, y: 0, z: 0 } },
          },
        },
        sideView: {
          scene: {
            camera: { eye: { x: 0, y: 2, z: 0 } },
          },
        },
        topView: {
          scene: {
            camera: { eye: { x: 0, y: 0, z: 2 } },
          },
        },
      },
    };
  },
  mounted() {
    this.plotData();
  },
  watch: {
    csvData() {
      this.plotData();
    },
    view(newVal, oldVal) {
      if (newVal !== oldVal) {
        this.plotData();
      }
    },
  },
  methods: {
    safeString(value) {
      return value.toString();
    },
    plotData() {
      if (!this.csvData || this.csvData.length === 0) return;

      const layout = {
        ...this.layout,
        ...this.layouts[this.view],
        scene: {
          xaxis: {
            title: "X, 10³ км",
            tickformat: ",d",
          },
          yaxis: {
            title: "Y, 10³ км",
            tickformat: ",d",
          },
          zaxis: {
            title: "Z, 10³ км",
            tickformat: ",d",
          },
          camera: {
            eye: { x: 1.25, y: 1.25, z: 1.25 },
          },
        },
        showlegend: true,
      };

      const x = this.csvData.map((row) =>
        (parseFloat(row.x) / 1000).toFixed(3)
      );
      const y = this.csvData.map((row) =>
        (parseFloat(row.y) / 1000).toFixed(3)
      );
      const z = this.csvData.map((row) =>
        (parseFloat(row.z) / 1000).toFixed(3)
      );

      // Расчёт величины скорости
      const v = this.csvData.map((row) =>
        Math.sqrt(
          Math.pow(parseFloat(row.vx), 2) +
            Math.pow(parseFloat(row.vy), 2) +
            Math.pow(parseFloat(row.vz), 2)
        )
      );

      const xMin = Math.min(...x);
      const xMax = Math.max(...x);
      const yMin = Math.min(...y);
      const yMax = Math.max(...y);
      const zMin = Math.min(...z);
      const zMax = Math.max(...z);

      const xOffset = (xMax - xMin) * 0.1;
      const yOffset = (yMax - yMin) * 0.1;
      const zOffset = (zMax - zMin) * 0.1;

      const hoverText3D = x.map(
        (xVal, i) =>
          `X: ${xVal} 10³км<br>Y: ${y[i]} 10³км<br>Z: ${z[i]} 10³км<br>V: ${v[i]} км/с`
      );
      const hoverTextXY = x.map(
        (xVal, i) => `X: ${xVal} 10³км<br>Y: ${y[i]} 10³км`
      );
      const hoverTextXZ = x.map(
        (xVal, i) => `X: ${xVal} 10³км<br>Z: ${z[i]} 10³км`
      );
      const hoverTextYZ = y.map(
        (yVal, i) => `Y: ${yVal} 10³км<br>Z: ${z[i]} 10³км`
      );

      const trace3D = {
        x: x,
        y: y,
        z: z,
        mode: "lines",
        line: {
          color: v,
          colorscale: "Jet",
          showscale: true,
          width: 5,
          colorbar: {
            title: { text: "V (км/с)", side: "right" },
            thickness: 10,
            len: 0.5,
          },
        },
        text: hoverText3D, // Ховер-текст для 3D
        hoverinfo: "text",
        name: "Траектория",
        type: "scatter3d",
      };

      const traceXY = {
        x: x,
        y: y,
        z: Array(x.length).fill(zMin - zOffset),
        mode: "lines",
        line: {
          color: "gray",
          colorscale: "Jet",
          width: 3,
        },
        text: hoverTextXY, // Ховер-текст для XY
        hoverinfo: "text",
        name: "Проекция на XY",
        type: "scatter3d",
      };

      const traceXZ = {
        x: x,
        y: Array(y.length).fill(yMin - yOffset),
        z: z,
        mode: "lines",
        line: {
          color: "gray",
          colorscale: "Jet",
          width: 3,
        },
        text: hoverTextXZ, // Ховер-текст для XZ
        hoverinfo: "text",
        name: "Проекция на XZ",
        type: "scatter3d",
      };

      const traceYZ = {
        x: Array(x.length).fill(xMin - xOffset),
        y: y,
        z: z,
        mode: "lines",
        line: {
          color: "gray",
          colorscale: "Jet",
          width: 3,
        },
        text: hoverTextYZ, // Ховер-текст для YZ
        hoverinfo: "text",
        name: "Проекция на YZ",
        type: "scatter3d",
      };

      const config = {
        displayModeBar: false,
        responsive: true,
      };

      if (document.getElementById(this.chartId)) {
        Plotly.newPlot(
          this.chartId,
          [trace3D, traceXY, traceXZ, traceYZ],
          layout,
          config
        );
      } else {
        console.error(
          `Element with ID ${this.chartId} does not exist.`
        );
      }
    },
  },
};
</script>

<style scoped>
.plotly-chart {
  width: 100%;
  height: 100%;
}
</style>
