<!-- MapImg.vue -->
<template>
  <div class="container">
    <h2>Карта начальных условий</h2>
    <!-- Индикатор загрузки -->
    <div v-if="isLoading" class="loading-message">
      <div class="spinner"></div>
      <p>Загрузка...</p>
    </div>
    <div v-else>
      <div v-if="!noOrbitsFound">
        <div id="plotly-chart" ref="plotlyChart"></div>
        <div class="info">
          <p
            v-if="
              selectedX !== null && selectedZ !== null && !isLoading
            "
          >
            Selected X = {{ (selectedX * 1000).toFixed(5) }}<br />
            Selected Z = {{ (selectedZ * 1000).toFixed(5) }}
          </p>
          <button
            v-if="
              selectedX !== null && selectedZ !== null && !isLoading
            "
            @click="applyOrbit"
            class="orbit-button"
          >
            Показать информацию об орбите
          </button>
        </div>
      </div>
      <div v-else class="no-orbits-message">
        <p>К сожалению, орбиты с такими параметрами не найдены.</p>
      </div>
    </div>
  </div>
</template>

<script>
import Plotly from "plotly.js-dist-min";
import { mapState, mapActions, mapMutations, mapGetters } from "vuex";

export default {
  data() {
    return {
      selectedX: null,
      selectedZ: null,
      noOrbitsFound: false,
      isLoading: false,
      isFetching: false, // Добавлен флаг для предотвращения повторных запросов
      initialized: false,
      selectedTraceIndex: null,
    };
  },
  computed: {
    ...mapState([
      "filters",
      "allOrbitsData",
      "selectedOrbitsData",
      "orbitId",
    ]),
    ...mapGetters(["getOrbitData"]),
  },
  watch: {
    orbitId(newOrbitId) {
      // Отслеживаем изменение orbitId и обновляем выделение орбиты
      if (this.initialized) {
        this.updateHighlightOrbit(newOrbitId);
      }
    },
    selectedOrbitsData() {
      // Перерисовываем график при изменении данных
      this.$nextTick(() => {
        this.plotData(
          this.allOrbitsData,
          this.selectedOrbitsData.data
        );
      });
    },
  },
  mounted() {
    console.log("MapImg компонент смонтирован");
    this.fetchOrbits();
  },
  beforeUnmount() {
    const plotlyChart = this.$refs.plotlyChart;
    if (plotlyChart) {
      plotlyChart.removeAllListeners("plotly_click");
    }
  },
  methods: {
    ...mapActions(["updateOrbitId"]),
    ...mapMutations([
      "setAllOrbitsData",
      "setSelectedOrbitsData",
      "infoOrbit",
      "resetInformationOrbit",
    ]),

    applyOrbit() {
      if (this.selectedX !== null && this.selectedZ !== null) {
        this.infoOrbit();
        this.$emit("orbit-applied", {
          x: this.selectedX,
          z: this.selectedZ,
        });
      } else {
        console.error("No orbit is selected.");
      }
    },

    async fetchOrbits() {
      if (this.isFetching) {
        console.log("fetchOrbits уже выполняется");
        return;
      }
      this.isFetching = true;
      this.isLoading = true;
      console.log("fetchOrbits вызван");
      try {
        if (!this.allOrbitsData) {
          const responseForAllPoints = await fetch(
            `https://orbital-catalog.auditory.ru/api/v1/orbits/map`
          );
          if (!responseForAllPoints.ok) {
            throw new Error("Failed to fetch all orbits data");
          }
          const allData = await responseForAllPoints.json();
          console.log("All Data:", allData);
          this.setAllOrbitsData(allData);
        } else {
          console.log("Используется кэшированные данные всех орбит");
        }

        const queryParams = new URLSearchParams();
        const {
          amplitudeX,
          period,
          amplitudeY,
          jacobiConstant,
          amplitudeZ,
          family,
        } = this.filters;

        if (period?.from)
          queryParams.append("t_min", Number(period.from));
        if (period?.to)
          queryParams.append("t_max", Number(period.to));

        if (amplitudeX?.from)
          queryParams.append("ax_min", Number(amplitudeX.from));
        if (amplitudeX?.to)
          queryParams.append("ax_max", Number(amplitudeX.to));

        if (amplitudeY?.from)
          queryParams.append("ay_min", Number(amplitudeY.from));
        if (amplitudeY?.to)
          queryParams.append("ay_max", Number(amplitudeY.to));

        if (amplitudeZ?.from)
          queryParams.append("az_min", Number(amplitudeZ.from));
        if (amplitudeZ?.to)
          queryParams.append("az_max", Number(amplitudeZ.to));

        if (jacobiConstant?.from)
          queryParams.append("cj_min", Number(jacobiConstant.from));
        if (jacobiConstant?.to)
          queryParams.append("cj_max", Number(jacobiConstant.to));

        if (family?.trim()) queryParams.append("tag", family.trim());

        const queryString = queryParams.toString();
        if (
          this.selectedOrbitsData &&
          this.selectedOrbitsData.queryString === queryString
        ) {
          console.log(
            "Используется кэшированные данные выбранных орбит"
          );
        } else {
          const url = queryString
            ? `https://orbital-catalog.auditory.ru/api/v1/orbits/map?${queryString}`
            : `https://orbital-catalog.auditory.ru/api/v1/orbits/map`;

          console.log("URL выбранных орбит:", url);

          const responseForSelectedPoints = await fetch(url);
          if (!responseForSelectedPoints.ok) {
            throw new Error("Failed to fetch selected orbits data");
          }
          const selectedData = await responseForSelectedPoints.json();
          console.log("Selected Data:", selectedData);

          this.setSelectedOrbitsData({
            data: selectedData,
            queryString: queryString,
          });

          // Нет необходимости в `currentSelectedData`, так как используем customdata
        }

        this.noOrbitsFound =
          this.selectedOrbitsData.data.length === 0;
      } catch (error) {
        console.error("Error fetching orbits data:", error);
        this.noOrbitsFound = true;
        this.selectedX = null;
        this.selectedZ = null;
        this.resetInformationOrbit();
        this.updateOrbitId(null); // Сброс orbitId при ошибке
      } finally {
        this.isFetching = false;
        this.isLoading = false;

        this.$nextTick(() => {
          this.plotData(
            this.allOrbitsData,
            this.selectedOrbitsData.data
          );
        });
      }
    },

    plotData(allData = [], selectedData = []) {
      console.log("Plotting Data:", { allData, selectedData });

      if (!allData.length) {
        this.noOrbitsFound = true;
        this.selectedX = null;
        this.selectedZ = null;
        this.resetInformationOrbit();
        return;
      }

      const plotlyChart = this.$refs.plotlyChart;
      console.log("Plotly Chart Element:", plotlyChart);
      if (plotlyChart) {
        if (!this.initialized) {
          this.initializeChart(allData, selectedData);
          this.initialized = true;
        } else {
          this.updateHighlightOrbit(this.orbitId);
        }
      } else {
        console.error(
          "No DOM element with ref 'plotlyChart' exists on the page."
        );
      }

      if (this.orbitId) {
        this.updateHighlightOrbit(this.orbitId);
      }
    },

    initializeChart(allData, selectedData) {
      const plotlyChart = this.$refs.plotlyChart;
      if (!plotlyChart) {
        console.error("Element with ref 'plotlyChart' not found.");
        return;
      }

      // Разделяем selectedData на две группы
      const selectedDataFaint = selectedData.filter(
        (orbit) => orbit.id === 12103 // Только id 12103 считаем тусклыми
      );
      const selectedDataBright = selectedData.filter(
        (orbit) => orbit.id !== 12103
      );

      // Обработка данных для всех орбит
      const xAll = allData
        .map((row) => parseFloat(row["x"]))
        .filter((val) => !isNaN(val));
      const zAll = allData
        .map((row) => parseFloat(row["z"]))
        .filter((val) => !isNaN(val));
      const xAxis = xAll.map((x) => x / 1000);
      const zAxis = zAll.map((z) => z / 1000);

      const traceAll = {
        x: xAxis,
        y: zAxis,
        mode: "markers",
        marker: { size: 2, color: "rgba(128, 128, 128, 0.5)" },
        hoverinfo: "none",
        showlegend: false,
        type: "scatter",
        name: "Все орбиты",
      };

      // Обработка данных для ярких точек
      const xBright = selectedDataBright
        .map((row) => parseFloat(row["x"]))
        .filter((val) => !isNaN(val));
      const zBright = selectedDataBright
        .map((row) => parseFloat(row["z"]))
        .filter((val) => !isNaN(val));
      const vBright = selectedDataBright
        .map((row) => parseFloat(row["v"]))
        .filter((val) => !isNaN(val));
      const orbitIdBright = selectedDataBright.map(
        (orbit) => orbit.id
      ); // Добавляем orbitId

      const traceSelectedBright = {
        x: xBright.map((x) => x / 1000),
        y: zBright.map((z) => z / 1000),
        mode: "markers",
        marker: {
          size: 3,
          color: vBright,
          colorscale: "Jet",
          showscale: true,
          colorbar: {
            title: { text: "V, км/с", side: "right" },
          },
        },
        hoverinfo: "text",
        text: xBright.map(
          (x, i) =>
            `X: ${x.toFixed(2)} км<br>Z: ${zBright[i].toFixed(
              2
            )} км<br>V: ${vBright[i].toFixed(2)} км/с`
        ),
        customdata: orbitIdBright, // Добавляем customdata
        showlegend: false,
        type: "scatter",
        name: "Отобранные орбиты",
      };

      // Обработка данных для тусклых точек (id 12103)
      const xFaint = selectedDataFaint
        .map((row) => parseFloat(row["x"]))
        .filter((val) => !isNaN(val));
      const zFaint = selectedDataFaint
        .map((row) => parseFloat(row["z"]))
        .filter((val) => !isNaN(val));
      const vFaint = selectedDataFaint
        .map((row) => parseFloat(row["v"]))
        .filter((val) => !isNaN(val));
      const orbitIdFaint = selectedDataFaint.map((orbit) => orbit.id); // Добавляем orbitId

      const traceSelectedFaint = {
        x: xFaint.map((x) => x / 1000),
        y: zFaint.map((z) => z / 1000),
        mode: "markers",
        marker: {
          size: 3,
          color: vFaint,
          colorscale: "Jet",
          showscale: false, // Не показываем шкалу для тусклых точек
          opacity: 0.3, // Уменьшаем непрозрачность для тусклости
        },
        hoverinfo: "text",
        text: xFaint.map(
          (x, i) =>
            `X: ${x.toFixed(2)} км<br>Z: ${zFaint[i].toFixed(
              2
            )} км<br>V: ${vFaint[i].toFixed(2)} км/с`
        ),
        customdata: orbitIdFaint, // Добавляем customdata
        showlegend: false,
        type: "scatter",
        name: "Отобранные орбиты (тусклые)",
      };

      const traceHighlight = {
        x: [],
        y: [],
        mode: "markers",
        marker: { size: 10, color: "red", symbol: "star" },
        text: [],
        showlegend: false,
        name: "Выбранная орбита",
        type: "scatter",
      };

      Plotly.newPlot(plotlyChart, [
        traceAll,
        traceSelectedBright,
        traceSelectedFaint,
        traceHighlight,
      ]);

      plotlyChart.on("plotly_click", this.handlePlotlyClick);

      if (this.orbitId) {
        this.updateHighlightOrbit(this.orbitId);
      }
    },

    handlePlotlyClick(data) {
      const point = data.points[0];
      const orbitId = point.customdata;

      if (orbitId) {
        // Находим орбиту по orbitId
        const selectedOrbit = this.selectedOrbitsData.data.find(
          (orbit) => orbit.id === orbitId
        );

        if (selectedOrbit) {
          this.selectedX = parseFloat(selectedOrbit.x) / 1000;
          this.selectedZ = parseFloat(selectedOrbit.z) / 1000;
          this.updateOrbitId(selectedOrbit.id);
        } else {
          console.warn(
            `Orbit with id ${orbitId} not found in selected data.`
          );
        }
      } else {
        console.warn("No orbitId found in clicked point.");
      }
    },

    updateHighlightOrbit(orbitId) {
      const plotlyChart = this.$refs.plotlyChart;
      if (!plotlyChart || !plotlyChart.data) return;

      if (!orbitId) {
        // Очистить выделение, если нет orbitId
        Plotly.restyle(
          plotlyChart,
          { x: [[]], y: [[]], text: [[]] },
          [3] // Трасса highlight
        );
        this.selectedX = null;
        this.selectedZ = null;
        return;
      }

      // Ищем орбиту в выбранных данных (яркие и тусклые)
      let selectedOrbit = this.selectedOrbitsData.data.find(
        (orbit) => orbit.id === orbitId
      );

      // Если орбита не найдена, ищем её в общем списке
      if (!selectedOrbit) {
        console.warn(
          `Orbit with id ${orbitId} not found in selected data, searching in all data.`
        );
        selectedOrbit = this.allOrbitsData.find(
          (orbit) => orbit.id === orbitId
        );
      }

      // Если орбита найдена, отображаем её как звезду
      if (selectedOrbit) {
        this.selectedX = parseFloat(selectedOrbit.x) / 1000;
        this.selectedZ = parseFloat(selectedOrbit.z) / 1000;
        const selectedV = parseFloat(selectedOrbit.v);
        Plotly.restyle(
          plotlyChart,
          {
            x: [[this.selectedX]],
            y: [[this.selectedZ]],
            text: [
              [
                `X: ${(this.selectedX * 1000).toFixed(2)} км<br>Z: ${(
                  this.selectedZ * 1000
                ).toFixed(2)} км<br>V: ${selectedV.toFixed(2)} км/с`,
              ],
            ],
            hovertemplate: "%{text}<extra></extra>",
          },
          [3] // Трасса highlight
        );
        Plotly.restyle(
          plotlyChart,
          {
            "marker.size": [[12]],
            "marker.color": [["red"]],
            "marker.symbol": [["star"]],
          },
          [3] // Трасса highlight
        );
        console.log(`Highlight updated to orbitId: ${orbitId}`);
      } else {
        console.warn(
          `Orbit with id ${orbitId} not found in all data.`
        );
      }
    },
  },
};
</script>

