  padding: 32px 16px;
  box-shadow: 0 2px 12px 2px rgba(145, 158, 225, 0.18),
    0 1px 3px 0px rgba(145, 158, 225, 0.14),
    0 0 3px -1px rgba(145, 158, 225, 0.25);
  border-radius: 10px;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 30px;
}

.loading-message {
  display: flex;
  flex-direction: column;
  align-items: center;
  font-family: var(--font-family);
  font-size: 20px;
  color: var(--main-color);
  text-align: center;
  padding: 20px;
  border: 2px solid var(--main-color);
  border-radius: 10px;
  margin-bottom: 20px;
}

.spinner {
  border: 4px solid rgba(0, 0, 0, 0.1);
  width: 36px;
  height: 36px;
  border-radius: 50%;
  border-left-color: #09f;
  animation: spin 1s linear infinite;
  margin-bottom: 10px;
}

@keyframes spin {
  to {
    transform: rotate(360deg);
  }
}

.no-orbits-message {
  font-family: var(--font-family);
  font-size: 20px;
  color: var(--main-color-darker);
  text-align: center;
  padding: 20px;
  border: 2px solid var(--main-color);
  border-radius: 10px;
}

.orbit-button {
  border-radius: 10px;
  width: 419px;
  height: 48px;
  background: var(--main-color);
  font-family: var(--font-family);
  font-weight: 500;
  font-size: 20px;
  text-align: center;
  color: #fbfbfb;
  border: none;
  margin-top: 20px;
}

.orbit-button:hover {
  background: var(--main-color-darker);
}

#plotly-chart {
  width: 100%; /* Занимает всю ширину родителя */
  min-width: 1000px;
  height: 600px;
}

.info {
  display: flex;
