<template>
  <div class="container-to-from">
    <p>от</p>
    <input type="text" v-model="from" @input="emitUpdate" />
    <p>до</p>
    <input type="text" v-model="to" @input="emitUpdate" />
  </div>
</template>

<script>
export default {
  props: {
    filterName: {
      type: String,
      required: true,
    },
  },
  data() {
    return {
      from: null,
      to: null,
    };
  },
  methods: {
    emitUpdate() {
      this.$emit("update", {
        filterName: this.filterName,
        value: {
          from: this.from,
          to: this.to,
        },
      });
    },
  },
};
</script>

<style scoped>
.container-to-from {
  display: flex;
  flex-wrap: nowrap;
  flex-direction: row;
  gap: 19px;
  align-items: center;
}
input {
  padding: 5px;
  border: 1.5px solid var(--main);
  border-radius: 10px;
  width: 100px;
}
input:focus-visible {
  outline: 0.5px solid var(--main);
}
</style>
